@observer
class StoryPublicFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
        // this.ref = createRef();
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

	likeStory = async (e) => {
        const { story } = this.props;
        const { id } = story;
        const storyId = id;
		const { storyStore } = this.props.stores;

        const { likeStory } = this.state;
        likeStory[storyId] = true;
        const likeIds = Object.keys(likeStory);
        this.setState({ likeStory, likeIds });
        setTimeout(() => {
            const { likeStory } = this.state;
            delete likeStory[storyId];
            const likeIds = Object.keys(likeStory);
            this.setState({ likeStory, likeIds });
        }, 1200);

		const response = await storyStore.likeStory({ id: storyId });
        if (response && response.status === 200) {
		}
	}

	toggleAddComment = (e) => {
		const storyId = e.target.closest('button').dataset.id;
		const { showAddComment } = this.state;
        if (showAddComment[storyId]) {
            delete showAddComment[storyId];
        } else {
            showAddComment[storyId] = true;
        }
		this.setState({
			showAddComment,
		}, () => {
			if (this.inputRef[storyId]) {
				this.inputRef[storyId].focus();
			}
		});
	}

    closeAllAddComments = () => {
        this.setState({
			showAddComment: {},
		});
    }

	toggleShowTeamSetting = (e) => {
		const storyId = e.target.closest('button').dataset.id;
		const { showTeamSetting } = this.state;
        if (showTeamSetting[storyId]) {
            delete showTeamSetting[storyId];
        } else {
            showTeamSetting[storyId] = true;
        }
		this.setState({
			showTeamSetting,
		});
	}

    viewImage = (e) => {
		const { id, idx } = e.target.closest('img').dataset;
        const { viewImageIdx } = this.state;
        viewImageIdx[id] = idx;
        this.setState({ viewImageIdx });
    }

    async loadStoriesPublicFeed(props = this.props) {
        const { hashtag } = props;
		const { storyStore } = props.stores;
		await storyStore.loadPublicFeed({ hashtag });
	}

    toggleMenu = (e) => {
        const { id } = e.target.closest('i').dataset;
		const { showMenu = {} } = this.state;
        showMenu[id] = !showMenu[id];
		this.setState({
            showDeleteStory: {},
            showEdit: {},
			showMenu,
		});
	}

    toggleEdit = (e) => {
        const { id } = e.target.closest('button').dataset;
		const { showEdit = {} } = this.state;
        showEdit[id] = !showEdit[id];
		this.setState({
            showDeleteStory: {},
			showEdit,
            showMenu: {},
		});
	}

    toggleDeleteStory = (e) => {
        const { id } = e.target.closest('button').dataset;
		const { showDeleteStory = {} } = this.state;
        showDeleteStory[id] = !showDeleteStory[id];
		this.setState({
			showDeleteStory,
            showEdit: {},
            showMenu: {},
		});
	}

    toggleAdminDetails = () => {
        const { showAdminDetails } = this.state;
        this.setState({
            showAdminDetails: !showAdminDetails,
        });
        this.setState({
            showEdit: {},
            showMenu: {},
		});
    }

    reportStory = async (e) => {
        const { id } = e.target.closest('button').dataset;
        const { storyStore } = this.props.stores;
        await storyStore.reportStory({ id });
        this.setState({
            showEdit: {},
            showMenu: {},
		});
    }

    adminRemoveStory = async (e) => {
        const { id } = e.target.closest('button').dataset;
        const { storyStore } = this.props.stores;
        await storyStore.adminHideStory({ id });
        this.setState({
            showEdit: {},
            showMenu: {},
		});
    }

    deleteStory = async (e) => {
        const { id } = e.target.closest('button').dataset;
        const { storyStore } = this.props.stores;
        await storyStore.saveField(parseInt(id, 10), 'offline', 1, true);
        storyStore.publicFeedUpdate(parseInt(id, 10), 'offline', 1);
        this.setState({
            storyBody: '',
			showDeleteStory: {},
            showEdit: {},
            showMenu: {},
		});
    }

    doneEdit = () => {
        this.setState({
			showDeleteStory: {},
            showEdit: {},
            showMenu: {},
		});
    }

    showStoryDetail = (e) => {
        if (e.target.localName === 'a') {
            return false;
        }
        const { id } = e.target.closest('#storyDetail').dataset;
        if (id) {
            route(`/stories/${id}`);
        }
    }

    shareLink = (e) => {
        const { url, title, text } = e.target.closest('button').dataset;
        const { appState } = this.props.stores;
        appState.shareUrl({ url, title, text });
    }

    displayTeam = (e, teamId) => {
        e.preventDefault();
        e.stopPropagation();
        const { appState } = this.props.stores;
        appState.openDrawer('publicTeam', { teamId, height: '80vh' });
	}

    openInbox = (e, fromuser) => {
        e.preventDefault();
        e.stopPropagation();
        const { appState } = this.props.stores;
        appState.openDrawer('inboxDetail', { fromuser, height: '80vh' });

    }

    // IntersectionObserver
    // componentDidMount() {
    //     const { addIntersectionObserver } = this.props;
    //     if (addIntersectionObserver) {
    //         addIntersectionObserver(this.ref);
    //     }
    // }

    // componentWillUnmount() {
    //     const { removeIntersectionObserver } = this.props;
    //     if (removeIntersectionObserver) {
    //         removeIntersectionObserver(this.ref);
    //     }
    // }
    // /IntersectionObserver

    render() {
        const {
            showAddComment,
            showDate,
            viewImageIdx,
            showMenu = {},
            showEdit = {},
            showDeleteStory = {},
            showTeamSetting,
            likeStory,
            likeIds,
            showAdminDetails
        } = this.state;
        const { story, showAllComments, sessionid, onTeamClick = () => {}, onUserClick = () => {} } = this.props;
        const { userStore, storyStore, appState } = this.props.stores;
        const { user: currentUser = {}, isAdmin } = userStore;
        const { darkmode, hasShareApi } = appState;

        const team = storyStore.findPublicTeam(story.team) || {};
        const user = storyStore.findPublicUser(story.user) || {};
        const image = mu.getImage({ user, team, priority: 'user', object: story });

        const isTeamAdmin = (team && team.members) ? team.members.some(m => m === currentUser.id) : false;

        let shortname;
        let name;
        let username;
        let userLink;
        if (team && team.name) {
            shortname = mu.displayNameShort(team);
            name = mu.displayName(team);
            username = mu.displayName(user);
            if (user.settings && currentUser.uuidv4 !== user.uuidv4) {
                userLink = user.settings.hideProfile ? null : `/inbox/${user.uuidv4}`;
            }
        } else if (user && user.id) {
            shortname = mu.displayNameShort(user);
            name = mu.displayName(user);
        }
        const { likes, comments, reports = [] } = story;
        const hasLikedStory = story.likes && story.likes.findIndex(e => e.user === currentUser.id) > -1;
        const totalFollowers = team.followers ? team.followers.length : 0;

        const isYourTeam = team.members && team.members.indexOf(currentUser.id) > -1;
        const isFollowing = team.followers && team.followers.indexOf(currentUser.id) > -1;
        const hasRequestedFollow = team.followRequests && team.followRequests.findIndex(e => e.id === currentUser.id) > -1;

        const { currentLocationGeocode = {}, yrWeather = [] } = story;
        const views = mu.getViews(story.statistics);
        const adminData = mu.getAdminData(story.statistics);

        return (<>
            {/* <div
                class='row pt-3 pb-3 bg-light'
                style={`${story.offline ? 'opacity: 0.4;' : ''}`}
                ref={c => this.ref = c}
                data-id={story.id}
                data-type={'story'}
                data-user={story.user}
                data-team={story.team}
                data-title={story.title}
                data-sessionid={sessionid}
            >
                <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative rounded rounded-lg py-3 bg-${darkmode ? 'black' : 'white'} shadow-sm`}> */}
                    {(isAdmin || (currentUser.id === user.id)) ? <>
                        <span class='float-right font-weight-lighter text-muted'>
                            <i class='fas fa-ellipsis-h' onClick={this.toggleMenu} data-id={story.id} />
                        </span>
                    </> : <>
                        {!isYourTeam && !isFollowing && !hasRequestedFollow && team.public > 0 && <span class='float-right'>
                            <PublicTeam stores={this.props.stores} team={team} buttonOnly={true} simple={true} classNames={'btn btn-link text-primary'} />
                        </span>}
                    </>}
                    {showMenu[story.id] && <>
                        <div class='position-absolute bg-light p-2 border rounded rounded-lg shadow-sm' style='top: 30px; right: 10px; width: 60%; z-index: 999999;'>
                            {currentUser.id === user.id ? <>
                                <button class='btn btn-link btn-block text-left' onClick={this.toggleEdit} data-id={story.id}><i class='fas fa-edit' /> Edit</button>
                                <hr />
                                <button class='btn btn-link btn-block text-left text-danger' onClick={this.toggleDeleteStory} data-id={story.id}><i class='fas fa-trash-alt' /> Delete</button>
                            </> : <>
                                <button class='btn btn-link btn-block text-left' onClick={this.reportStory} data-id={story.id}><i class='fas fa-flag' /> Report</button>
                                {isAdmin && <>
                                    <hr />
                                    <button class='btn btn-link btn-block text-left text-danger' onClick={this.adminRemoveStory} data-id={story.id}>
                                        <i class='fas fa-eraser' /> Remove from feed
                                    </button>
                                </>}
                            </>}
                            <button class='btn btn-link btn-block text-left' onClick={this.toggleAdminDetails}>
                                <i class='fas fa-user-cog' /> Toggle admin details
                            </button>
                        </div>
                    </>}

                    <div class='d-flex position-relative'>
                        <div
                            class={`position-relative text-center border rounded-circle imageRounded imageRoundedMedium`}
                            style={image ? `background-image: url("${image}"); background-size: cover;` : ''}
                        >
                            {!image && <div class='text-muted' style='font-size: 20px; padding-top: 3px;'>{shortname}</div>}
                        </div>
                        <div class='flex-grow-1 pl-3 text-overflow' style='line-height: 1.2em;'>
                            <h5 class='mb-1 position-relative' style={`line-height: 1.0em; ${story.offline ? 'text-decoration: line-through;' : ''}`}>
                                {team.public ? <>
                                    <a href={`/teams/public/${team.uuidv4}`} onClick={(e) => this.displayTeam(e, team.uuidv4)} class='stretched-link'>{name}</a>
                                    {/* <a href={`/stories/teams/public/${team.uuidv4}`} class='stretched-link'>{name}</a> */}
                                </> : <>
                                    {name}
                                </>}

                                {isYourTeam && !team.public && <button type='button' class='btn btn-sm btn-link text-muted'
                                    data-id={story.id}
                                    onClick={this.toggleShowTeamSetting}
                                >
                                    <i class='fas fa-exclamation-triangle' />
                                </button>}
                            </h5>
                            <div class='mb-2' style={`${story.offline ? 'text-decoration: line-through;' : ''}`}>
                                {story.pinned && <>
                                    <small class='text-muted mr-2'>
                                        <i class='fas fa-thumbtack' />
                                    </small>
                                </>}
                                {story.type === 2 ? <>
                                    <small class='text-muted'><Text id='story.sponsored'>Sponsored</Text> -</small>
                                </> : <>
                                    {story.date && <small class='text-muted' onClick={this.toggleDate}>
                                        {showDate ? util.formatDate(story.date, { locale: 'no-NB', hour12: false, hour: '2-digit', minute: '2-digit' }) : util.formatDistance(story.date, new Date(), { locale: 'no-NB' })}
                                    </small>}
                                </>}
                                {story.public === 1 ? <>
                                    <small class='ml-2 text-muted'>
                                        <i class='fas fa-globe-europe' />
                                    </small>
                                </> : <>
                                    <small class='ml-2 text-muted'>
                                        <i class='fas fa-users' /> ({util.format(totalFollowers)})
                                    </small>
                                </>}
                                {username && <>
                                    <small class='ml-2 text-muted'>
                                        {userLink ? <>
                                            <i class='fas fa-user' /> <a href={userLink} onClick={(e) => this.openInbox(e, user.uuidv4)}>{username}</a>
                                        </> : <>
                                            <i class='fas fa-user' /> {username}
                                        </>}
                                    </small>
                                </>}
                                {currentLocationGeocode && currentLocationGeocode.formattedCounty && <>
                                    <small class='ml-2 text-muted'>
                                        <i class='fas fa-map-marker-alt' /> {currentLocationGeocode.formattedCounty}
                                    </small>
                                </>}
                            </div>
                            {showAdminDetails && isAdmin && <div
                                class='d-flex flex-row flex-nowrap bg-light border-top border-bottom py-2'
                                style='overflow: auto;'
                            >
                                <small class='text-muted mr-2'>Adm:</small>
                                {reports && reports.length > 0 && <>
                                    <small class='text-danger mr-2'>
                                        <i class='fas fa-flag' /> {reports.length}
                                    </small>
                                </>}
                                {currentLocationGeocode && currentLocationGeocode.formatted_address && <>
                                    <small class='text-muted'>
                                        <i class='fas fa-map-marker-alt' /> {currentLocationGeocode.formatted_address}
                                    </small>
                                </>}
                                {user.deviceInfo && <>
                                    <span class='badge badge-secondary ml-2 float-right font-weight-light'>
                                        {['platform', 'manufacturer', 'model'].map(e => <>
                                            <span class='ml-2 text-white'>
                                                {user.deviceInfo[e]}
                                            </span>
                                        </>)}
                                    </span>
                                </>}

                                {adminData && <>
                                    <small class='ml-2 text-muted'>
                                        V: {adminData.count}
                                    </small>
                                    <small class='ml-2 text-muted'>
                                        R: {adminData.ranking} {util.isNumber(adminData.rankingTrend) && <><i class={mu.getClassForTrend(adminData.rankingTrend)} /></>}
                                    </small>
                                    <small class='ml-2 text-muted'>
                                        D: {util.format(adminData.durationAvg / 1000, 1)}s {util.isNumber(adminData.durationAvgTrend) && <><i class={mu.getClassForTrend(adminData.durationAvgTrend)} /></>}
                                    </small>
                                    {story.statisticsCurrent && story.statisticsCurrent.totalInteractions > 0 && <small class='ml-2 text-muted'>
                                        S: {util.format(story.statisticsCurrent.totalInteractions, 0)}
                                    </small>}
                                    {story.shadowTags && story.shadowTags.map(tag => <>
                                        <span class='badge badge-secondary ml-2 float-right font-weight-light'>{tag}</span>
                                    </>)}
                                </>}
                                {/* {story.tags && <>
                                    {util.isArray(story.tags) && story.tags.map(tag => <>
                                        <small class='ml-2 text-muted'>
                                            {tag}
                                        </small>
                                    </>)}
                                </>} */}
                            </div>}
                        </div>
                    </div>

                    {showEdit[story.id] || showDeleteStory[story.id] ? <>
                        <div class='row mt-2'>
                            <div class='clearfix col-12' style={`${story.offline ? 'text-decoration: line-through;' : ''}`}>
                                {showEdit[story.id] && <>
                                    <StoryEditInline
                                        stores={this.props.stores}
                                        story={story}
                                        key={`story-edit-${story.id}`}
                                        callback={this.doneEdit}
                                    />
                                </>}
                                {showDeleteStory[story.id] && <div class='clearfix mt-3 mb-3'>
                                    <button class='btn btn-danger float-left' onClick={this.deleteStory} data-id={story.id}><i class='fas fa-trash-alt' /> Delete</button>
                                    <button class='btn btn-secondary float-right' onClick={this.toggleDeleteStory} data-id={story.id}><i class='fas fa-times-circle' /> Cancel</button>
                                </div>}
                            </div>
                        </div>
                    </> : <>

                        {(!story.type || story.type == 1) && <StoryTypeRegular stores={this.props.stores} story={story} key={`story-regular-view-${story.id}`} likeStory={this.likeStory} likeRef={likeStory} keys={likeIds} />}
                        {story.type == 2 && <StoryTypeAd stores={this.props.stores} story={story} key={`story-ad-view-${story.id}`} likeStory={this.likeStory} likeRef={likeStory} keys={likeIds} />}
                        {story.type == 3 && <StoryTypeArticle stores={this.props.stores} story={story} key={`story-article-view-${story.id}`} likeStory={this.likeStory} likeRef={likeStory} keys={likeIds} />}

                        <div class='d-flex justify-content-between mt-2'>
                            <div class='mr-2'>
                                <LikesAndComments
                                    stores={this.props.stores}
                                    likes={likes}
                                    comments={comments}
                                    type='story'
                                    onTeamClick={onTeamClick}
                                    onUserClick={onUserClick}
                                />
                            </div>
                            {views > 0 && <>
                                <div>
                                    <Views
                                        stores={this.props.stores}
                                        views={views}
                                        object={story}
                                        type='story'
                                        isTeamAdmin={isTeamAdmin}
                                        isAdmin={isAdmin}
                                        onTeamClick={onTeamClick}
                                        onUserClick={onUserClick}
                                    />
                                </div>
                            </>}
                        </div>

                        {showTeamSetting[story.id] && <>
                            <small>
                                <div class='alert alert-info' role='alert'>
                                    <span class='display-4 float-left'>
                                        <i class='fas fa-exclamation-triangle mr-3 mb-3' />
                                    </span>
                                    <p>
                                        <Text id='story.alert-text-1'>Your team is not visible and it's not possible to request friendship with your team.</Text>
                                    </p>
                                    <p>
                                        <Text id='story.alert-text-2'>If you want to make your team visible so other can request to follow your team, you can find the setting here:</Text>
                                    </p>
                                    <p>
                                        <a href={`/teams/edit/${team.id}`}><Text id='story.alert-text-3'>Edit team</Text></a>
                                    </p>
                                </div>
                            </small>
                        </>}

                        <div>
                            <div class='row'>
                                <div class='col text-center'>
                                    <button
                                        type='button'
                                        class={`storyImage btn btn-sm btn-outline-${hasLikedStory ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedStory ? 'text-primary' : ''} text-overflow`}
                                        data-id={story.id}
                                        onClick={this.likeStory}
                                    >
                                        <i class={`${hasLikedStory ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> {hasLikedStory ? <Text id='comments.has-liked'>Liked</Text> : <Text id='comments.like'>Lik</Text>}
                                    </button>
                                </div>
                                <div class='col text-center'>
                                    <button
                                        type='button'
                                        class='btn btn-sm btn-outline-secondary btn-block text-overflow'
                                        data-id={story.id}
                                        onClick={this.toggleAddComment}
                                    >
                                        <i class='fas fa-comment' /> <Text id='comments.comment-add'>Kommentar</Text>
                                    </button>
                                </div>
                                {hasShareApi ? <>
                                    <div class='col text-center'>
                                        <button class='btn btn-sm btn-outline-secondary btn-block text-overflow'
                                            onClick={this.shareLink}
                                            data-url={`https://themusher.app/share/story/${story.uuidv4}`}
                                            data-title={`title: ${story.title}`}
                                        >
                                            <i class='fas fa-share' /> <Text id='story.share'>Del</Text>
                                        </button>
                                    </div>
                                </> : <>
                                    <div class='col text-center'>
                                        <a class='btn btn-sm btn-outline-secondary btn-block text-overflow'
                                            href={`https://themusher.app/share/story/${story.uuidv4}`}
                                            target='_blank'
                                            rel='noreferrer'
                                            data-native
                                        >
                                            <i class='fas fa-share' /> <Text id='story.share'>Del</Text>
                                        </a>
                                    </div>
                                </>}

                            </div>
                        </div>

                        <div class='mt-2'>
                            <StoryComments
                                stores={this.props.stores}
                                story={story}
                                col='10'
                                offset='2'
                                compact={false}
                                showAllComments={showAllComments}
                                initCall={this.closeAllAddComments}
                            />
                            {showAddComment[story.id] && <StoryAddComment
                                stores={this.props.stores}
                                story={story}
                                inputRef={c => this.inputRef[story.id] = c}
                                inputRefObject={this.inputRef}
                                col='10'
                                offset='2'
                                key={`story-comment-${story.id}`}
                            />}
                        </div>
                    </>}
                {/* </div> */}
            {/* </div> */}
        </>);
    }
}

export default StoryPublicFeed;
